import type {
  MoSerializationPlugin, MoServerSerializationPlugin,
} from '@ali/4ever-cangjie';
import markdownToHTML from './markdownToHTML';
import jsonMLToValue from './jsonMLToValue';
import valueToJsonML from './valueToJsonML';

export default function createSerializer(): MoSerializationPlugin {
  return {
    markdownToHTML,
    jsonMLToValue,
    valueToJsonML,
  };
}

export function createServerSerializationPlugin(): MoServerSerializationPlugin {
  return {
    valueToJsonML,
    jsonMLToValue,
  };
}
